<template>
  <section class="nes-container with-title" :class="{ 'is-dark': darkmode }">
    <span class="title">{{ data.title }}</span>
    <rough-pie
      :data="tmpData"
      title="Regions"
      :colors="['red', 'orange', 'blue', 'skyblue']"
      roughness="8"
    ></rough-pie>

    <!-- Buttons -->
    <slot />
    <!---->
    <!---->
  </section>
</template>

<script>
import showdown from "showdown";
import { RoughPie } from "vue-roughviz";

export default {
  name: "barchart",
  components: {
    RoughPie,
  },
  data() {
    return {
      showdownRef: new showdown.Converter(),
      tmpData: {
        labels: ["North", "South", "East", "West"],
        values: [10, 5, 8, 3],
      },
    };
  },
  methods: {
    markdownify(input = "") {
      if (this.showdownRef) {
        return this.showdownRef.makeHtml(input);
      }
      return input;
    },
  },
  mounted() {},
  props: {
    data: Object,
    image_url: String,
    image_alttext: String,
    darkmode: Boolean,
  },
};
</script>

